

import {observable,action,computed,autorun }  from "mobx";

import {axios} from "&"

class ShopCar{
    @observable carList = [];
    


    @action getCarList= ()=>{

        axios.post("/react/getShopcarInfo").then(res=>{
            this.carList = res.data.carList;
        })
    }

    @action changeOneCheck=(goodId,checked)=>{
        axios.post("/react/checkOneAndAll",{
            goodId,
            checked
        }).then(res=>{
            this.carList = this.carList.map((item)=>{
                if(goodId==item.goodId){
                    item.checked = checked;
                }
                return item;
            })
        })
        
    }

    @action changeAllCheck=(checked)=>{
        axios.post("/react/checkOneAndAll",{
            checked
        }).then(res=>{
            this.carList = this.carList.map((item)=>{
                item.checked = checked;
                return item;
            })
        })
    }

    @action changeOneCount = (goodId,flag)=>{

        axios.post("/react/changecount",{
            goodId,
            flag
        }).then(res=>{

        })
        this.carList = this.carList.map(item=>{
            if(item.goodId==goodId){
                item.count += flag ? 1:-1;
            }
            return item;
        })
    }

    @action changeCountByInp = (goodId,count)=>{
        axios.post("/react/changecount",{
            goodId,
            count
        }).then(res=>{
            
        })
        this.carList = this.carList.map(item=>{
            if(item.goodId==goodId){
                item.count  = parseInt(count);
            }
            return item;
        })
    }

    @action delChecked = ()=>{
        // checked 
        var goodIdList = [];
        this.carList.forEach((item)=>{
            if(item.checked){
                goodIdList.push(item.goodId);
            }
        });
        console.log(goodIdList);
        axios.post("/react/delSelect",{
            goodIdList
        }).then(res=>{
            this.carList =  this.carList.filter(item=>!item.checked);
        })
    }

    @computed get carNum(){
        var num = 0;
        this.carList.forEach((item)=>{
            num+=item.count*1;
        })
        return num;
    }

    @computed get checkNum(){
        var num = 0;
        this.carList.forEach((item)=>{
            if(item.checked){
                num+=item.count*1;
            }
        })
        return num;
    }

    @computed get total(){
        var total = 0;
        this.carList.forEach((item)=>{
            if(item.checked){
                total += item.count * item.goodInfo.price * 1;
            }
            
        })
        return total;
    }

    @computed get quan(){
        var flag = true;   
        this.carList.forEach((item)=>{
            if(!item.checked){
                flag = false;
            }
        })
        return flag;
    }


}

export default new ShopCar();



